<template>
  <div class="send-message">
    <Button class="btn-back" type="warning" @click="back">返回</Button>
    <Table class="message-table" stripe :columns="messageHeader" :data="messageData"></Table>
    <div class="tag-modal" v-if="modalShow">
      <h3>请选择要发送的标签</h3>
      <br>
      <RadioGroup v-model="tagId">
        <Radio v-for="(tag, index) in tagList" :key="index" :label="tag.tagId">{{tag.name}}</Radio>
      </RadioGroup>
      <br>
      <Button class="btn" type="warning" @click="cancel">取消</Button>
      <Button class="btn" type="success" @click="submit">确定</Button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      modalShow: false,
      tagList: [],
      id: "",
      tagId: 0,
      mediaId: "",
      messageData: [],
      messageHeader: [
        {
          key: "media_id",
          title: "media_id",
          align: "center",
          width: 100
        },
        {
          key: "name",
          title: "name",
          align: "center"
        },
        {
          key: "url",
          title: "url",
          align: "center",
          width: 80
        },
        {
          key: "update_time",
          title: "update_time",
          align: "center"
        },
        {
          title: "图文内容",
          align: "center",
          render: (h, params) => {
            let content = params.row.content.news_item.length != 0 ? params.row.content.news_item[0].content : '';
            return h(
              "span",
              {
                props: {}
              },
              content
            );
          }
        },
        {
          title: "图文图片",
          align: "center",
          render: (h, params) => {
            let url = params.row.content.news_item.length != 0 ? params.row.content.news_item[0].url : '';
            return h(
              "span",
              {
                props: {}
              },
              url
            );
          }
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.modalShow = true;
                      that.id = params.row._id;
                      that.mediaId = params.row.mediaId;
                      that.tagList();
                    }
                  }
                },
                "发送消息"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
    console.log(this.$route.params)
  },
  methods: {
    back() {
      this.$router.push({
        name: "群发列表"
      });
    },
    showDataList() {
      this.$axios
        .get("/material/show", { params: { code: this.$route.params.code } })
        .then(res => {
          this.messageData = res.data.data;
        });
    },
    getTagList() {
      this.$axios.get("/material/tag").then(res => {
        this.tagList = res.data.data;
      });
    },
    cancel() {
      this.modalShow = false;
    },
    submit() {
      this.$axios
        .get("/material/send", {
          params: {
            id: this.id,
            tagId: this.tagId,
            mediaId: this.mediaId
          }
        })
        .then(res => {
          this.$Message.info(res.data.data);
        });
    }
  }
};
</script>

<style scoped>
.message-table {
  width: 100%;
  clear: both;
}
.tag-modal {
  width: 300px;
  height: 180px;
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: rgba(0, 0, 0, 0.7);
  z-index: 100000;
  color: #fff;
}
.tag-modal .btn {
  margin-top: 30px;
  margin-right: 20px;
}

.btn-back {
  margin-bottom: 30px;
  float: left;
}
</style>

